(function() {
  var initMap;

  console.log("CoffeeScript is OK");

  $(document).ready(function() {
    return console.log("jQuery is working");
  });

  initMap = function() {
    var map;
    map = new google.maps.Map(document.getElementById('map'), {
      center: {
        lat: -34.397,
        lng: 150.644
      },
      zoom: 8
    });
    return console.log("Map function triggered");
  };

  console.log("Map function loaded");

}).call(this);
